import "./styles.css";
import Editor from "./components/Editor";
import { makeStyles } from "@material-ui/core";
import useLocalStorage from "./hooks/useLocalStorage";
import { ThemeProvider, createMuiTheme } from "@material-ui/core/styles";

import { useState, useEffect } from "react";

import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import ListItemText from "@material-ui/core/ListItemText";
import Drawer from "@material-ui/core/Drawer";
import { Paper } from "@material-ui/core";

import { FaHtml5, FaCss3Alt, FaCode } from "react-icons/fa";
import IconButton from "@material-ui/core/IconButton";
import Brightness3Icon from "@material-ui/icons/Brightness3";
import Brightness7Icon from "@material-ui/icons/Brightness7";

const drawerWidth = 200;

const useStyles = makeStyles((theme) => ({
  page: {
    background: "#f9f9f9",
    width: "100%"
  },
  root: {
    display: "flex",
    flex: 1
  },
  drawer: {
    width: drawerWidth
  },
  drawerPaper: {
    width: drawerWidth
  },
  toggle: {
    width: "50px",
    height: "50px",
    backgroundColor: "grey",
    borderRadius: "50%",
    alignContent: "center",
    justifyContent: "center",
    position: "fixed",
    right: "40px",
    bottom: "40px"
  }
}));

export default function App() {
  const classes = useStyles();

  const [html, setHtml] = useLocalStorage("<h1>Hello</h1>", "");
  const [css, setCss] = useLocalStorage(
    `body{
    background-color:white;
  }`,
    ""
  );
  const [js, setJs] = useLocalStorage("js", "");
  const [srcDoc, setSrcDoc] = useState("");
  const [active, setActive] = useLocalStorage("FirstCard");

  useEffect(() => {
    const timeout = setTimeout(() => {
      setSrcDoc(`
      <html>
        <body>${html}</body>
        <style>${css}</style>
        <script>${js}</script>
      </html>
      `);
    }, 250);
    return () => clearTimeout(timeout);
  }, [html, css, js]);

  const [darkMode, setDarkMode] = useLocalStorage(false);
  const icon = darkMode ? <Brightness3Icon /> : <Brightness7Icon />;
  const theme = createMuiTheme({
    palette: {
      type: darkMode ? "dark" : "light"
    }
  });

  return (
    <>
      <ThemeProvider theme={theme}>
        <Paper>
          <div className="partition">
            <Drawer
              className={classes.drawer}
              variant="permanent"
              classes={{ paper: classes.drawerPaper }}
              anchor="left"
            >
              <List>
                <ListItem button onClick={() => setActive("FirstCard")}>
                  <FaHtml5 style={{ marginRight: 5 }} />
                  <ListItemText primary="HTML" />
                </ListItem>
              </List>
              <List>
                <ListItem button onClick={() => setActive("SecondCard")}>
                  <FaCss3Alt style={{ marginRight: 5 }} />
                  <ListItemText primary="CSS" />
                </ListItem>
              </List>
              <List>
                <ListItem button onClick={() => setActive("ThirdCard")}>
                  <FaCode style={{ marginRight: 5 }} />
                  <ListItemText primary="JAVASCRIPT" />
                </ListItem>
              </List>
            </Drawer>
            <div className="editor">
              {active === "FirstCard" && (
                <Editor
                  language="xml"
                  displayName="HTML"
                  value={html}
                  onChange={setHtml}
                  theme={darkMode}
                />
              )}
              {active === "SecondCard" && (
                <Editor
                  language="css"
                  displayName="CSS"
                  value={css}
                  onChange={setCss}
                  theme={darkMode}
                />
              )}
              {active === "ThirdCard" && (
                <Editor
                  language="javascript"
                  displayName="JS"
                  value={js}
                  onChange={setJs}
                  theme={darkMode}
                />
              )}
            </div>
            <div className="output">
              <div className="editor_title">LIVE VIEW</div>
              <iframe
                srcDoc={srcDoc}
                title="output"
                sandbox="allow-scripts"
                frameBorder="0"
                height="100%"
                width="100%"
              ></iframe>
            </div>
          </div>
          <div className={classes.toggle}>
            <IconButton
              style={
                darkMode
                  ? {
                      alignSelf: "center",
                      color: "white",
                      backgroundColor: "#404040"
                    }
                  : {
                      alignSelf: "center",

                      color: "orange",
                      backgroundColor: "#b3f0ff"
                    }
              }
              edge="end"
              aria-label="mode"
              onClick={() => setDarkMode(!darkMode)}
            >
              {icon}
            </IconButton>
          </div>
        </Paper>
        
      </ThemeProvider>
      

    </>
  );
}
